html {
	font-size: 62.5%;
}

body {
	margin: 0;
	padding: 0;
	font-family: sans-serif;
}

.demo {
	width: 40rem;
	height: 40rem;
	margin: 2rem auto 0;
	background-size: cover;
}

.demo table td {
	width: 14rem;
	height: 14rem;
	text-align: center;
	vertical-align: middle;
	font-size: 2.4rem;
	color: #333;
	font-index: -999;
}

.draw button {
	background-color: #fa8072;
	color: #fff;
	width: 14rem;
	height: 14rem;
}

.active {
	background-color: yellow;
}

.hidden {
	background-color: #8ed9f9;
}

.scroll_div {
	height: 2rem;
	overflow: hidden;
	white-space: nowrap;
	width: 54rem;
	margin-left: 10px;
	background-color: #a9a9a9;
	border-radius: 0.4rem;
	position: absolute;
	left: 25rem;
	top: 50rem;
	color: #fff;
}

.scroll_div span {
	padding-left: 2rem;
}

.scroll_div span i {
	color: #f08080;
}

.scroll_begin,
.scroll_end {
	display: inline;
}


